.App {
  height: 100%;
  width: 100%;
}

.webmap {
  height: 100%;
  flex: auto;
}

.symbolBuilder {
  height: 100vh;
  flex: none;
  display: flex;
  flex-direction: column;
}
.popoverContent {
  padding: 5px;
}
.symbolType {
  padding: 10px 10px 0px 10px;
}

.symbolTypeBtns {
  flex: 1;
}

.symbolLayersEditor {
  display: flex;
  flex-direction: column;
  text-align: left;
  flex: 1;
  overflow: hidden;
  /* padding: 1.5em 20px; */
  color: #737373;
  text-decoration: none;
  font-weight: 400;
}

.layerPanel {
  flex-grow: 1;
  padding: 10px 10px;
}

.layerItem canvas {
  max-width: 20px;
  max-height: 20px;
  cursor: pointer;
}

.layerItem .preview {
  width: 20px;
  height: 20px;
  cursor: pointer;
  display: flex;
  justify-content: center;
  align-items: center;
  box-shadow: 0 1px 1px 0 rgba(0, 0, 0, 0.1), 0 1px 1px 0 rgba(0, 0, 0, 0.1);
}

.primitiveGallery {
  width: 100%;
  display: flex;
  flex-flow: row;
  flex-wrap: wrap;
  flex: auto;
  height: auto;
  overflow: auto;
}

.symbolBuilder_Title h1 {
  padding: 0.5em 20px;
  font-size: 0.95em;
  text-decoration: none;
  font-weight: 400;
}

.symbolBuilder hr {
  border: 0.5px solid #e7ecf4;
  margin: 0;
  width: 100%;
}

.helpIcon {
  float: right;
  display: inline;
  cursor: pointer;
}

.primitiveThumbnail {
  height: auto;
  width: 64px;
  height: 64px;
  display: flex;
  align-items: center;
  justify-content: center;
}

.primitiveThumbnail img {
  max-width: 80%;
  max-height: 80%;
}

.symbolPreview {
  height: 20vh;
  min-height: 120px;
  width: 20vw;
  margin: auto;
}

.inputLabel,
.numberInputLabel {
  display: block;
  /* width: 100px; */
  font-size: var(--calcite-font-size--1);
  color: var(--calcite-ui-text-1);
  margin-bottom: 5px;
}

.checkBoxLabel {
  display: inline;
  font-size: var(--calcite-font-size--1);
  color: var(--calcite-ui-text-1);
  margin-left: 5px;
}

.selectLabel {
  font-size: var(--calcite-font-size--1);
  color: var(--calcite-ui-text-1);
  margin-bottom: 5px;
}

.SymbolGallery {
  flex: 1;
  overflow: hidden;
  display: flex;
  flex-direction: column;
  padding: 5px;
}

pre {
  text-align: left;
}
